import React from 'react';
import IPFS from 'ipfs-core';
import { ethers } from 'ethers';

import Breadcrumb from '../../components/breadcrumb';

import Hashes from '../../contracts/Hashes.json';

class Validate extends React.Component {
  _ipfs;

  constructor() {
    super();
    this.state = { validating: false };
    this.handleClick = this.handleClick.bind(this);
    this._calculateHash = this._calculateHash.bind(this);
    this._addToBlockchain = this._addToBlockchain.bind(this);
  }

  async componentDidMount() {
    this._ipfs = await IPFS.create();
  }

  async componentWillUnmount() {
    await this._ipfs.stop();
  }

  async _calculateHash(file) {
    try {
      const buffer = await file.arrayBuffer();
      const { path } = await this._ipfs.add(buffer);

      return path;
    } catch (error) {
      console.log(error);
      return false;
    }
  }

  async _addToBlockchain(hash) {
    await window.ethereum.request({ method: 'eth_requestAccounts' });

    if (typeof window.ethereum !== 'undefined') {
      const contractAddress = '0x7fa9262CE8348860917268bA299C19B668Ae5F7c';
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();

      const contract = new ethers.Contract(contractAddress, Hashes.abi, signer);
      try {
        const res = await contract.add(hash);
        return res;
      } catch (error) {
        if (error.code === 4001) {
          return 'reject';
        }

        console.log(error);
        return false;
      }
    } else {
      return false;
    }
  }

  async handleClick(e) {
    e.preventDefault();
    this.setState({ validating: true });

    const file = document.getElementById('file-input').files[0];
    if (!file) {
      alert('Tambahkan file');
      this.setState({ validating: false });
      return;
    }

    const hash = await this._calculateHash(file);
    if (!hash) {
      alert('Gagal mendapatkan hash');
      this.setState({ validating: false });
      return;
    }

    const res = await this._addToBlockchain(hash);
    if (!res) {
      alert('Transaksi gagal');
      this.setState({ validating: false });
      return;
    }

    if (res === 'reject') {
      alert('Transaksi ditolak');
      this.setState({ validating: false });
      return;
    }

    alert('Transaksi sedang diproses, cek console untuk detail');
    console.log('IPFS Link:', 'https://ipfs.io/ipfs/' + hash);
    console.log(
      'Transaction Link:',
      'https://ropsten.etherscan.io/tx/' + res.hash
    );

    this.setState({ validating: false });
  }

  render() {
    const btnClass = this.state.validating
      ? 'block bg-gray-300 text-white rounded-md px-3 py-2 w-full cursor-wait'
      : 'block bg-green-500 text-white rounded-md px-3 py-2 w-full';

    const btnTxt = this.state.validating
      ? 'Memproses..'
      : 'Tambah dan Validasi';

    return (
      <>
        <Breadcrumb links={[{ text: 'Validasi', link: '/' }]} />
        <div className="grid grid-cols-1 md:grid-cols-3 mt-4">
          <div className="flex flex-col justify-center items-center bg-white p-3 rounded-md">
            <form className="w-full">
              <label className="space-y-1 block mb-4">
                <p>Nama</p>
                <input
                  className="bg-gray-200 rounded-md px-3 w-full py-2"
                  disabled
                  type="text"
                  value="Muhammad Faizal"
                />
              </label>
              <label className="space-y-1 block mb-4">
                <p>NIM</p>
                <input
                  className="bg-gray-200 rounded-md w-full px-3 py-2"
                  disabled
                  type="text"
                  value="1822490645"
                />
              </label>
              <label className="space-y-1 block mb-8">
                <p>Ijazah</p>
                <input
                  disabled={this.state.validating}
                  id="file-input"
                  type="file"
                />
              </label>

              <button
                onClick={(e) => this.handleClick(e)}
                disabled={this.state.validating}
                className={btnClass}
              >
                {btnTxt}
              </button>
            </form>
          </div>
        </div>
      </>
    );
  }
}

export default Validate;
