import React from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';

import './index.css';

import Navbar from './layouts/navbar';
import Sidebar from './layouts/sidebar';
import Main from './layouts/main';

import Validasi from './pages/validasi';
import Verifikasi from './pages/verifikasi';

window.addEventListener('DOMContentLoaded', () => {
  const updateSidebar = () => {
    if (window.innerWidth < 1024) {
      document.body.classList.toggle('sidebar-open', false);
      document.body.classList.toggle('sidebar-mini', false);
    }
  };
  window.addEventListener('resize', updateSidebar);

  const toggleSidebar = () => {
    if (window.innerWidth < 1024) {
      document.body.classList.toggle('sidebar-open');
      document.body.classList.toggle('sidebar-mini', false);
    } else {
      document.body.classList.toggle('sidebar-mini');
      document.body.classList.toggle('sidebar-open', false);
    }
  };

  const toggle = document.getElementById('toggle-sidebar');
  toggle.addEventListener('click', () => {
    toggleSidebar();
  });

  const overlay = document.getElementById('overlay');
  overlay.addEventListener('click', (e) => {
    e.preventDefault();
    toggleSidebar();
  });

  const sidebarLogo = document.getElementById('sidebar-logo');
  sidebarLogo.addEventListener('click', () => {
    if (window.innerWidth < 1024) {
      document.body.classList.toggle('sidebar-open');
      document.body.classList.toggle('sidebar-mini', false);
    }
  });

  const sidebarMenuLink = document.getElementsByClassName('sidebar-menu-link');
  for (let link of sidebarMenuLink) {
    link.addEventListener('click', () => {
      if (window.innerWidth < 1024) {
        document.body.classList.toggle('sidebar-open');
        document.body.classList.toggle('sidebar-mini', false);
      }
    });
  }
});

document.body.classList.add('text-gray-700');
document.body.classList.add('bg-green-600');

const App = () => {
  return (
    <BrowserRouter>
      <Sidebar />
      <Navbar />
      <Switch>
        <Main>
          <Route path="/" component={null} />
          <Route path="/validasi" component={Validasi} />
          <Route path="/verifikasi" component={Verifikasi} />
        </Main>
      </Switch>
    </BrowserRouter>
  );
};

export default App;
