import React from 'react';
import IPFS from 'ipfs-core';
import { ethers } from 'ethers';

import Breadcrumb from '../../components/breadcrumb';

import Hashes from '../../contracts/Hashes.json';

class Verify extends React.Component {
  _ipfs;

  constructor() {
    super();
    this.state = { verifying: false };
    this.handleClick = this.handleClick.bind(this);
    this._calculateHash = this._calculateHash.bind(this);
    this._checkInBlockchain = this._checkInBlockchain.bind(this);
  }

  async componentDidMount() {
    this._ipfs = await IPFS.create();
  }

  async componentWillUnmount() {
    await this._ipfs.stop();
  }

  async _calculateHash(file) {
    try {
      const buffer = await file.arrayBuffer();
      const { path } = await this._ipfs.add(buffer, { onlyHash: true });

      return path;
    } catch (error) {
      console.log(error);
      return false;
    }
  }

  async _checkInBlockchain(hash) {
    await window.ethereum.request({ method: 'eth_requestAccounts' });

    if (typeof window.ethereum !== 'undefined') {
      const contractAddress = '0x7fa9262CE8348860917268bA299C19B668Ae5F7c';
      const provider = new ethers.providers.Web3Provider(window.ethereum);

      const contract = new ethers.Contract(
        contractAddress,
        Hashes.abi,
        provider
      );
      try {
        const res = await contract.getWithCID(hash);
        return res;
      } catch (error) {
        console.log(error);
        return false;
      }
    } else {
      return false;
    }
  }

  async handleClick(e) {
    e.preventDefault();
    this.setState({ verifying: true });

    const file = document.getElementById('file-input').files[0];
    if (!file) {
      alert('Tambahkan file');
      this.setState({ verifying: false });
      return;
    }

    const hash = await this._calculateHash(file);
    if (!hash) {
      alert('Gagal mendapatkan hash');
      this.setState({ verifying: false });
      return;
    }

    const res = await this._checkInBlockchain(hash);
    if (!res) {
      alert('File tidak terverifikasi');
      this.setState({ verifying: false });
      return;
    }

    alert('File terverifikasi, cek console untuk detail');
    console.log('IPFS Link:', 'https://ipfs.io/ipfs/' + res);

    this.setState({ verifying: false });
  }

  render() {
    const btnClass = this.state.verifying
      ? 'block bg-gray-300 text-white rounded-md px-3 py-2 w-full cursor-wait'
      : 'block bg-green-500 text-white rounded-md px-3 py-2 w-full';

    const btnTxt = this.state.verifying ? 'Memproses..' : 'Verifikasi';

    return (
      <>
        <Breadcrumb links={[{ text: 'Verifikasi', link: '/' }]} />
        <div className="grid grid-cols-1 md:grid-cols-3 mt-4">
          <div className="flex flex-col justify-center items-center bg-white p-3 rounded-md">
            <form className="w-full">
              <label className="space-y-1 block mb-4">
                <p>Ijazah</p>
                <input
                  disabled={this.state.verifying}
                  id="file-input"
                  type="file"
                />
              </label>

              <button
                onClick={(e) => this.handleClick(e)}
                disabled={this.state.verifying}
                className={btnClass}
              >
                {btnTxt}
              </button>
            </form>
          </div>
        </div>
      </>
    );
  }
}

export default Verify;
